	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/all-these-physics-games/index.md absPath of file >>> MarkdownRemark","html":"<p>It seems like fun physics games are on the rise - there's <a href=\"http://www.sciencemuseum.org.uk/launchpad/launchball/\">Launchball</a>, which I've <a href=\"http://www.psyked.co.uk/adobe/flash/launchball-an-awesome-flash-game.htm\">mentioned before</a> - but there also seems to be a rise in the numbers of other simple physics games created in Flash. Maybe its because of as3 libraries like <a href=\"http://www.cove.org/ape/index.htm\">APE</a> making development easier, maybe commerical games like <a href=\"http://www.littlebigplanet.com/en_GB/\">Little Big Planet</a> are what drive innovation (imitation?), maybe its even just down to the arrival of Actionscript 3 and its enhanced processing abilities. Whatever the reason, games like <a href=\"http://fantasticcontraption.com/\">Fantasic Contraption</a> and <a href=\"http://www.miniclip.com/games/magic-pen/en/\">Magic Pen</a> have appeared on the scene, and are well worth a play.  (Just don't start playing when you should be working, otherwise you'll be in trouble.)</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/attackoftheclones-ddc1fc114cd0b8f8d2384803c5cbe39d-e8574.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 55.99999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"attackoftheclones\"\n        title=\"\"\n        src=\"/static/attackoftheclones-ddc1fc114cd0b8f8d2384803c5cbe39d-e8574.jpg\"\n        srcset=\"/static/attackoftheclones-ddc1fc114cd0b8f8d2384803c5cbe39d-c94d9.jpg 158w,\n/static/attackoftheclones-ddc1fc114cd0b8f8d2384803c5cbe39d-9885b.jpg 315w,\n/static/attackoftheclones-ddc1fc114cd0b8f8d2384803c5cbe39d-e8574.jpg 500w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>If you like those games, you can use classes like <a href=\"http://www.cove.org/ape/index.htm\">APE</a> to start building your own...</p>\n<h2>Links;</h2>\n<ul>\n<li><a href=\"http://www.cove.org/ape/index.htm\">APE - Actionscript Physics Engine</a></li>\n<li><a href=\"http://www.psyked.co.uk/adobe/flash/launchball-an-awesome-flash-game.htm\">Launchball</a></li>\n<li><a href=\"http://fantasticcontraption.com/\">Fantastic Contraption</a></li>\n<li><a href=\"http://www.miniclip.com/games/magic-pen/en/\">Magic Pen</a></li>\n<li><a href=\"http://www.littlebigplanet.com/en_GB/\">Little Big Planet</a></li>\n</ul>","frontmatter":{"title":"All these physics games...","date":"August 25, 2008","tags":["flash","ape","physics-games"],"url":null,"image":null,"description":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/just-how-easy-can-you-make-an-air-application-installation/","tags":["air","auto-updater"],"title":"Just how easy can you make an AIR application installation?","date":"2008-08-23T21:00:54.000Z","description":null,"image":null}},"next":{"frontmatter":{"path":"/blog/google-chrome-is-kinda-awesome/","tags":["javascript","chrome","google","webkit"],"title":"Google Chrome is kinda awesome...","date":"2008-09-10T13:58:27.000Z","description":null,"image":null}}}}

